import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import SignUp from './containers/Authetication/SignUp';
import Login from './containers/Authetication/Login';
import AppLayout from './containers/Authetication/AppLayout/index';
import { useSelector } from 'react-redux';

const App = (props) => {
  const { token } = useSelector(({ auth }) => auth);

  return (
    <Switch>
      {
        token ? <AppLayout {...props} /> :
          <>
            <Redirect to="/signin" />
            <Route exact path="/signin" component={Login} />
            <Route path="/signup" component={SignUp} />
          </>
      }

      {/*
      ============Add this routes if needed=======================
      <Route path="/unauthorized" component={Error404} />
      <Route path="/forgotpassword" component={ForgotPassword} />
      <Route path="/terms_condition" component={Terms} />
      */}


    </Switch>
  );
};


export default App;
